<template>
<div id="home-welcome">
  <div class="container">
    <div class="row">
      <div class="col l5 m6 s12 explanation">
        <div class="card">
          <div class="card-content">
            <span class="card-title red-text darken-2">Partiu Europa!</span>
            <p>
              Oi, me chamo <a target="_blank"
              href="http://www.pylestras.org/profile/12/">Italo Maia</a> e estou de partida
              para outro continente. Nessa viagem, alm de boas lembranas, poderei levar
              algumas coisas comigo, mas no tudo, por isso, montei esta lojinha com produtos
              usados, em bom estado e timos preos.
            </p>
          </div>
          <div class="card-action">
            <router-link to="/shop" class="red-text darken-2">Pechinchas</router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'Home',
  mounted: function () {
    this.$nextTick(function () {
      $('.parallax').parallax()
    })
  }
}
</script>

<style lang="scss" scoped>
#home-welcome {
  background-image: url(./le-me.jpg);
  background-position: 45% 40%;
  position: absolute;
  left: 0;
  top: 0;
  min-height: 100vh;
  min-width: 100vw;
  z-index: -1;

  > .container {
    margin-top: 5rem;
  }
}
.explanation {
  .card-title { font-weight: bold; }

  p {
    font-size: 22px;
    text-align: justify;
  }
}

@media only screen and (max-width: 600px) {
  .explanation p {
    font-size: 18px;
  }
}
</style>



