<template>
  <div class="card">
    <div class="card-image">
      <img :src="item.image" :alt="item.image_desc || 'indisponvel'" />
      <span class="card-title" v-text="item.title"></span>
    </div>
    <div class="card-content" v-if="item.description">
      <p v-text="item.description"></p>
    </div>
    <div class="card-content">
      <span>Preo</span>
      <strong class="floated right" title="produto disponvel" v-if="item.available">
        R$ {{ fprice(item.price) }}
      </strong>
      <del class="floated right" title="j foi vendido" v-else>
        R$ {{ fprice(item.price) }}
      </del>
    </div>
  </div>
</template>

<script>
export default {
  name: 'shop-item',
  props: ['item'],
  methods: {
    fprice: function (value) {
      return (value / 100).toFixed(2).replace('.', ',')
    }
  }
}
</script>

<style scoped>
.card .card-image .card-title {
  text-shadow: 2px 2px black;
  color: white;
  font-weight: bold; }
</style>



