<template>
  <div>
    <div class="section red white-text">
      <div class="container">
        <h3>Ateno</h3>
        <p>
          Todos os produtos aqui esto em bom estado (a no ser que a descrio diga o contrrio).
          Se o produto est "sem custo", o interessado fica responsvel por vir at mim
          buscar o produto, pois trata-se de uma doao.
        </p>
        <p>
          Interessados, por gentileza, entrar em contato por telegram, twitter ou e-mail (preferencialmente).
          Vendas apenas para residentes de <strong>Fortaleza/CE</strong> ou quem possa vir buscar.
        </p>
      </div>
    </div>
    <div class="section">
      <div class="container">
        <h3>Produtos</h3>
        <div class="row">
          <div class="col l4 s12" :key="item.title" v-for="item in items">
            <item :item="item"></item>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Item from './Item'

export default {
  name: 'Shop',
  components: {Item},
  data: function () {
    return {
      items: [
        {
          title: 'Carrinho com Motor',
          description: `
          Carrinho com chassi em alumnio, 2 motores e rodas de borracha.
          Funciona com 3 pilhas AA. Ideal para makers, pois precisa de
          alguns ajustes antes de estar pronto.
          `,
          price: 5000,
          image: require('@/assets/carrinho-maker.jpg'),
          available: true
        },
        {
          title: 'LG Pad G 8.3 Android 4.2.1 16GB',
          description: `
          Tablet LG de 1ghz com problema no touch (alguns pontos no respondem ao toque).
          Tela sem rachadura ou risco, Wifi e bateria em bom estado. Vai com capinha.
          `,
          price: 10000,
          image: require('@/assets/tablet-lg.jpg'),
          available: true
        },
        {
          title: 'GPS TOMTOM XXL 540',
          description: `
          GPS TOMTOM com todos os acessrios (suporte articulado, carregador veicular,
          cabo).
          Bateria ok, tela em perfeito estado e vai na caixinha.
          `,
          price: 16000,
          image: require('@/assets/tomtomxxl.jpg'),
          available: true
        }
      ]
    }
  }
}
</script>

<style scoped>
h3 {margin-top: 15px;}
</style>



